import React, { useState, useEffect } from 'react';
import Navbar from './Navbar';
import { Link } from 'react-router-dom';
import { Container, Row, Jumbotron, Col } from 'reactstrap';
import FooterPage from './footer';

function App() {
  const text = {left:"0%", top:"20vh", fontSize:"35px", fontFamily:"open sans"};
  const slide = ["fried.JPG", "plat.JPG", "spag.JPG", "fried2.JPG"]
  const [x , setX ] = useState(0);
  const [opac, setOpac] = useState("1")
  useEffect(()=>{
    const loop = setInterval(()=>{
        if(x === slide.length-1){
            setOpac("0"); setX(0); setOpac("1"); 
        }else{
          setOpac("0");  setX(x+1); setOpac("1");
        }
    }, 3000);
    return () => clearInterval(loop); 
  }, [slide, x]);
  const bg = {backgroundImage:`url(images/${slide[x]})`,opacity:`${opac}`, transition:"background 2s", height:"60vh", filter:"brightness(0.5)"}

  return (
    <div style={{height:"auto"}} className="w-100">
      <Navbar />
      <div  className="header d-flex bg-dark text-center w-auto" style={bg}>
      </div>
        <div className="text-center text-white position-absolute w-100" style={text}>Welcome to RelishCourt
          <br></br> <span style={{fontFamily:"cursive"}}>the luxurious street food</span> <br></br>
        <Link to="/order" ><button className="text-white shadow h4 m-3 bd-custom rounded-lg p-4"style={{backgroundColor:"inherit"}}>
          <h4>Go to our Kitchen</h4> </button></Link>
        </div>
      
      <Container className="col-12">
       <Row className="justify-content-around">
       <Col  md="4" xs="12" className="p-lg-2 h-auto">
          <div className="mt-1 bg-dark h-auto bd-custom w-100 text-center text-custom" style={{height:"50vh",fontSize:"24px", justifyContent:"center"}}>
              <img src="./images/stir.jfif" alt="" className="w-100 h-75"/>
          </div>
        </Col>
        <Col md="4" xs="12" className="p-lg-2 h-auto">
          <div className="mt-1 bg-dark h-auto bd-custom w-100 text-center text-custom" style={{height:"50vh",fontSize:"24px", justifyContent:"center"}}>
              <img src="./images/table.jfif" alt="" className="w-100 h-75"/>
          </div>
         </Col>
         <Col md="4" xs="12" className="p-lg-2 h-auto">
          <div className="mt-1 bg-dark h-auto bd-custom w-100 text-center text-custom" style={{height:"50vh",fontSize:"24px", justifyContent:"center"}}>
              <img src="./images/plat.JPG" alt="" className="w-100 h-75"/>
          </div>
         </Col>
         
       </Row>
       
      </Container>
      <Jumbotron className="bg-custom text-center m-auto text-white" style={{fontSize:"30px", fontFamily:"fantasy"}}>
          Aren't you hungry ? <br/>
          order and get satisfied ðŸ˜‹
      </Jumbotron>
      <FooterPage />
    </div>
  );
}
export default App;