import React, { useState } from 'react';
import { Nav} from 'reactstrap';
import { Link } from 'react-router-dom';
import Sidebar from './sidebar';
import { useSelector, useDispatch } from 'react-redux'
import { increment } from './actions/actions';

function Navbar(props) {
    const [dis, setDis] = useState("none")
    const num = useSelector(state => state.cartReducer.length)
    const did = useDispatch();
    const show = ()=>{
        did(increment(dis))
        if (dis === "none") {
            setDis("block")
        } else {
            setDis("none")
        }
        }
    return (   
    <nav className="navbar-dark bg-dark" >
        <div className="bg-dark w-auto" style={{display:"flex"}}>
            <div className="navbar-brand text-center col-lg-2 col-sm-10" >
                <Link to="/">
                    <img src="relishcourt.jpeg" alt="" width="100px" height="50px" className="rounded"></img>
                </Link>
            </div>
            <Nav className="d-sm pl-2 col-lg-9 col-sm-0" style={{fontFamily:"open sans", fontSize:"20px"}}>
                <Link className="text-center text-white link"  to="/">Home </Link>
                <Link className="text-center text-white link" to="/order" >Order Now</Link>
                <Link className="text-center text-white link" to="/contact"  >Contact Us</Link>
                <Link className="text-center text-white link" to="/about"  >About us</Link>
            </Nav>
            <div className="pt-3 pl-5 d-flex text-center text-white position-absolute" style={{right:"3%"}}>
               <Link to="/cart" className="text-white pt-1 pl-3"> <span style={{fontSize:"10px"}}>{num ? `${num}` : ''}</span>
                    <span className=""><i style={{fontSize:"18px", cursor:"pointer"}} className=" fa fa-shopping-cart"></i></span>
                </Link>
                <span className="pt-1 pl-3 d-lg"><i style={{fontSize:"18px", cursor:"pointer"}} className=" fa fa-bars" onClick={()=>show()}></i></span>
            </div>   
        </div>
        <Sidebar name={dis}/> 
     </nav> 
    );
}

export default Navbar;