import React from 'react';
import Navbar from './Navbar';
import { Input, Button, Container } from 'reactstrap';

function Contact(props) {
    return (
        <div>
            <Navbar />
            <Container className="container row m-auto">
                <div className="p-2 col-lg-6">
                    <div className="mt-lg-5 d-sm rounded">
                            <img src="relishcourt.jpeg" alt="" />
                    </div>
                </div>
                <div className="p-2 col-lg-6">
                    <form className="p-3 rounded mt-5 bg-light bd-custom">
                        <div className="text-custom text-center h3">Talk to us now <br/>
                        <span className="p-3">
                            <a href="https://instagram.com/relishcourt?igshid=1ukektg0kkh43"><i className="fa fa-instagram m-3 text-custom" style={{fontSize:"24px"}}></i></a>  
                            <a href="https://www.facebook.com/relish.court.9"> <i className="fa fa-facebook m-3 text-custom" style={{fontSize:"24px"}}></i></a>  
                            <a href="https://twitter.com/Relishcourt?s=08"><i className="fa fa-twitter m-3 text-custom" style={{fontSize:"24px"}}></i></a>   
                        </span>
                        </div>

                       <div className="p-2"> Name : </div>
                        <Input className="" type="text" />
                       <div className="p-2"> Email : </div>
                        <Input className="" type="text" />
                        <div className="p-2"> Number : </div>
                        <Input className="" type="text" /> 
                       <div className="p-2"> Message : </div>
                        <textarea className="w-100 rounded border" type="text" /> <br/>
                        <Button className="btn bg-custom m-3">Send</Button>
                    </form>
                </div>
            </Container>
        </div>
    );
}

export default Contact;