import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './components/App';
import * as serviceWorker from './serviceWorker';
import { Route, Switch } from 'react-router';
import { BrowserRouter } from 'react-router-dom';
import Home from './components/home';
import Contact from './components/contact_us';
import About from './components/about_us';
import Order from './components/order';
import Addfood from './components/addfood';
import Login from './components/login';
import Buy from './components/cart';
import NotFound from './notfound';
import { createStore } from 'redux';
import allReducers from './components/redux/store'
import { Provider } from 'react-redux';
import Checkout from './components/checkout';
// saving redux to the storage
const saveTostorage=(state)=>{
  let toString = JSON.stringify(state)
  localStorage.setItem('state', toString)
}
const loadState =()=>{
  const theState = JSON.parse(localStorage.getItem('state'));
  if (theState === null) {
      return undefined
  } else {
      return theState
  }
}
const store = createStore(allReducers, loadState())
store.subscribe(()=>{
  saveTostorage(store.getState())
})

ReactDOM.render(
<Provider store={store}>
  <BrowserRouter>
    <Switch>
    <Route exact path="/" component={App}></Route>
    <Route path="/home" component={Home}></Route>
    <Route path="/contact" component={Contact}></Route>
    <Route path="/about" component={About}></Route>
    <Route path="/order" component={Order}></Route>
    <Route path="/add" component={Addfood}></Route>
    <Route path="/login" component={Login}></Route>
    <Route path="/cart" component={Buy}></Route>
    <Route path="/checkout" component={Checkout}></Route>
    <Route path="*" component={NotFound}></Route>
    </Switch>
  </BrowserRouter>
</Provider>,
  document.getElementById('root')
);
serviceWorker.register();
