import React, { useEffect, useState } from 'react';
import Axios from 'axios';

function Edit(props) {
    const [food, setFood] = useState([])
    useEffect(()=>{
        // Axios.get('http://localhost:5000/users')
        // .then(res => { setFood(res.data.items) } )

        Axios.get('https://relishcourt.com/users')
        .then(res => { setFood(res.data.items) } )
    },[])

    const del=(id)=>{
      const data = {es : id}
      // Axios.post('http://localhost:5000/users/del', data)

      Axios.post('https://relishcourt.com/users/del', data)
    }
    return (
    <div class="table-responsive">
    <h5 className="text-center p-2 text-custom">Avaliable meals</h5>
    <table class="table table-striped col-lg-6 col-sm-12 m-auto bd-custom">
    <thead>
      <tr>
        <th scope="col">#</th>
        {/* <th scope="col">Edit</th> */}
        <th scope="col">Image</th>
        <th scope="col">Name</th>
        <th scope="col">Price</th>
        <th scope="col">Content</th>
        <th scope="col">Delete</th>
      </tr>
    </thead>
    <tbody>
      {food.map(({id, name, price, content, image},index)=>{
            return(
                <tr className="">
                    <th scope="row">{index+1}</th>
                    {/* <td>Edit</td> */}
                    <td> <img src={"./photos/"+image} height="30px" width="w-100" alt={name + " image"}/> </td> 
                    <td>{name}</td> 
                    <td className="d-flex"> <span>&#8358;</span> {price}</td> 
                    <td>{content}</td>
                    <button onClick={()=>{del(id)}} className="btn"  >del</button>
                </tr>
            );
        })}
    </tbody>
  </table></div>
    );}
export default Edit;
