import React from 'react';
import { Jumbotron } from 'reactstrap';

function Logout(props) {
   const logout =()=>{
        var con = window.confirm("Are you sure you want to logout")
        if (con) {
            localStorage.setItem("status",false)
            window.location.assign("/login")
        }
    }
    return (
    <div className="p-2 bd-custom rounded col-lg-6 m-auto " onClick={()=>{logout()} }>
    <Jumbotron className="h4 mb-0">
        Logout
    </Jumbotron>
    </div>
    );
}

export default Logout;
