import React, { useState } from 'react';
import { Form, Button } from 'reactstrap';

function Login(props) {
    const [mail, setMail] = useState("")
    const [pass, setPass] = useState("")
    
    const auth=(e)=>{e.preventDefault()
        if (mail === "reni@gmail.com" && pass === "reni1234") {
            window.location.assign("/add")
            localStorage.setItem("status",true)
        } else {
            alert("error in details")
        }
    }
    return (
        <Form className="p-2" onSubmit={(event)=>auth(event)}> 
            <br/>  <br/> <br/> <br/> <br/>
            <div className="p-2 bd-custom rounded col-lg-6 m-auto">
                <h5 className="text-center text-custom">Login to RelishCourt</h5>
                Email : <input type="email"  className="form-control" onChange={(e)=>{setMail(e.target.value)}} value={mail}/> <br/>
                Password : <input type="text"  className="form-control" onChange={(e)=>{setPass(e.target.value)}} value={pass} /> <br/>
                <Button className="bg-custom text-center" type="submit">Login </Button>
            </div>
        </Form>
    );
};

export default Login;