import React from 'react';
import { useEffect, useState } from 'react';
import Navbar from './Navbar';
import { Container, Button, Jumbotron, Col, Row } from 'reactstrap';
import FooterPage from './footer';
import { useSelector, useDispatch } from 'react-redux';
import { Remove, user_Def } from './actions/actions';

function Buy() {
    const [item, setItem] = useState([{name:"kenny"}]);
    let [tot, setTot] = useState(0)
    const cart = useSelector(state => state.cartReducer)
    const addPrices=(array)=>{
        const prices = [""];
        for (let i = 0; i < array.length; i++) {
            prices[i] = array[i].newPrice;
        }
        return prices
    }
   const sum = addPrices(cart).reduce(function(a,b){
       return a + b
   })
   const user_=(e,i,price, newPrice)=>{
       if(newPrice <=0){
           newPrice = price
           run(user_Def(1, i, price, price))
       }else{
        run(user_Def(e, i, price, newPrice))
       }
   }
    useEffect(()=> {setItem(cart);
        setTot(sum)
    } ,[cart, sum])

    const run = useDispatch();
    const remove =(i)=>{
        run(Remove(i))
    }
    return (
        <div className="bg-light">
            <Navbar />
            <Jumbotron className="h5 bg-whtie text-center">
                <Button className="float-left" onClick={()=>{window.history.back()}}> <i className="fa fa-angle-left"></i> </Button>
                 <Button className="float-right" onClick={()=>{window.location.assign("/checkout")}}>check out</Button>
                {cart ? `You have ${cart.length} item(s) in your cart` : 'Your cart is empty'} <br />
                <div className="p-4"><span>&#8358;</span>{tot}</div>
            </Jumbotron>
            <Container className="m-auto">
                   <Row className="justify-content-around">
            {cart ? item.map(({id,name, price, content, image, quantity, newPrice},index)=>{
                return(
                   <Col lg="4" xs="12" key={index}>
                        <div className="bd-custom bg-white m-1 rounded">
                            <img src={"./photos/"+image} alt={name + " image"} className="w-100 h-auto"/>
                            <div className="d-flex p-2">
                                <div className="w-50">
                                Name : {name} <br/>
                                Price : <span>&#8358;</span>{price} <br/>
                                subtotal : {newPrice} <br></br>
                                Content : {content}
                                </div>
                                <div className="w-50">
                                    <button className="border bg-danger btn float-right"
                                     onClick={()=>{remove(id)}}><i className="fa fa-remove"></i> </button> <br/>
                                    Quantity
                                    {/* <input type="number" className="border rounded-pill m-1" style={{width:"30%"}} 
                                    onChange={(e)=>user_(e.target.value, index, price, newPrice)} value={quantity}/> */}
                                    <div className="rounded-lg d-flex w-50">
                                        <button className="border bg-custom rounded-left w-25" 
                                            onClick={()=>user_(parseInt(quantity)-1, index, price, newPrice)}>-</button>
                                        <input type="text" className="w-50 border text-center" 
                                            onChange={(e)=>user_(e.target.value, index, price, newPrice)} value={quantity}/>
                                        <button className="border bg-custom rounded-right w-25"
                                            onClick={()=>user_(parseInt(quantity)+1, index, price, newPrice)}>+</button>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </Col>
                );
            }) : "empty"}   </Row>
            <Jumbotron className="bg-success mt-2 h4">
                Total <span>&#8358;</span>{tot}
            </Jumbotron>
               </Container>
        <FooterPage />
        </div>
    );
}

export default Buy;