import React, {  useState, useEffect } from 'react';
import { Button } from 'reactstrap';
import Axios from 'axios';

function NewItem(props) {

    const [file, setFile] = useState("");
    const [name, setName] = useState("");
    const [price, setPrice] = useState("");
    const [content, setContent] = useState("");
    const [prev, setPrev] = useState("./images/add.jfif");
    const [status, setStatus] = useState("")
    const Go=(e)=>{e.preventDefault();
        if(name !== "" || price !== "" || content !== "" || file !== ""){
            const formData = new FormData();
            formData.append('File', file) 
            formData.append('Name', name)
            formData.append('Price', price)
            formData.append('Content', content)
            // Axios.post("http://localhost:5000/settings",formData, {
            //     headers : {'Content-Type':'multipart/form-data'}
            // })
            // .then(res=>{alert(res.data.status)})
            
            Axios.post("https://relishcourt.com/settings",formData, {
                headers : {'Content-Type':'multipart/form-data'}
            })
            .then(res=>{setStatus(res.data.status)})
        }else{
            alert("Please fill in all")
        }
    }
    const Message =(props)=>{
        return(
        <div className={`alert alert-${props.color} border-${props.color} text-center d-${props.display}`} role="alert">
          {status ? " You successfully added a new food item " : "An error occured : check you network and make sure you upload a photo"}
        </div>
        )
    }
    useEffect(()=>{
    })

    const preview=(e)=>{
        setFile(e.target.files[0])
        setPrev(URL.createObjectURL(e.target.files[0]));
    }

    return (
        <div className="w-100 p-2">
            <h2 className="text-center p-3 text-custom">Admin, Welcome Reni</h2>
            <form className="p-2 bd-custom rounded col-lg-6 m-auto"  encType='multipart/form-data'  onSubmit={(event)=>{Go(event)}}>
                <Message color={status ? "success" : "danger" } display={status ? "block" : "none"} />
                <h5 className="text-center p-2 text-custom">Add New Food item</h5>
                <img src={prev} className="w-100" height="auto" alt="error"/>
                Image : <label for="img" className="p-3 m-1 btn bd-custom">Add image of food</label>
                         <input type="file" id="img" className="d-none" onChange={(event)=>{preview(event)}}/> <br/>
                Name : <input type="text" className="form-control" onChange={(e)=>{setName(e.target.value)}} value={name}/> 
                Price : <br/><span>&#8358;</span> <input type="number"  className="w-25 rounded border"  onChange={(e)=>{setPrice(parseInt(e.target.value))}} value={price}/> <br/>
                Content :<textarea  className="form-control"  onChange={(e)=>{setContent(e.target.value)}} value={content}></textarea> <br/>
                <div className="w-100 text-center"> <Button className="bg-custom col-2 m-auto"> Add </Button> </div>
            </form>
        </div>
    );
}

export default NewItem;