import React from "react";
import { Col, Container, Row, Nav } from "reactstrap";
import { Link } from "react-router-dom";

function FooterPage(props) {
  return (
    <div className="bg-dark text-white w-100 position-relative mt-5 pl-lg-5 pr-lg-5" style={{bottom:"auto"}}>
      <Container fluid className="text-center text-md-left">
        <Row className="p-2">
          <Col md="6">
            <h5 className="title">RelishCourt</h5>
            <p>
              The luxury street food
            </p>
          </Col>
          <Col md="6 text-center">
           <Nav className="text-center">
             <Link className="text-white text-center m-2" to="/">Home</Link>
             <Link className="text-white text-center m-2" to="/order">Order</Link>
             <Link className="text-white text-center m-2" to="/conact">Contact</Link>
             <Link className="text-white text-center m-2" to="/about">About</Link>
           </Nav>
          </Col>
        </Row>
      </Container>
        <Container className="m-auto w-75 text-center p-2">
            <span className="p-3">
          <a href="https://instagram.com/relishcourt?igshid=1ukektg0kkh43"><i className="fa fa-instagram m-3 text-custom" style={{fontSize:"20px"}}></i></a>  
          <a href="https://www.facebook.com/relish.court.9"><i className="fa fa-facebook m-3 text-custom" style={{fontSize:"20px"}}></i></a>   
          <a href="https://twitter.com/Relishcourt?s=08"><i className="fa fa-twitter m-3 text-custom" style={{fontSize:"20px"}}></i></a>   
            </span> <br/>
          &copy; {new Date().getFullYear()} Copyright: <a href="https://www.relishcourt.com"> Relishcourt.com </a>
        </Container>
    </div>
  );
}

export default FooterPage;