import React, { useState, useEffect } from 'react';
import { Form, Container, Col, Jumbotron, Button, Row, Label } from 'reactstrap';
import { useSelector } from 'react-redux';
import Navbar from './Navbar';

function Checkout(props) {
    const cart = useSelector(state => state.cartReducer)
    const [check, setCheck] = useState([])
    const [tot, settot] = useState([])
    const toArr=(array)=>{      
        let ex = [];
        for (let i = 0; i < array.length; i++) {
            ex[i] = array[i].newPrice
        }
        return ex
    }
    const sum = toArr(cart).reduce(function(a,b){
        return a + b
    })
    useEffect(()=>{
        setCheck(cart)
        settot(sum)
    },[cart,sum])


    return (
    <div className="">
        <Navbar />
        <Container className="m-auto">
            <Jumbotron className="text-center h3 text-custom">
            You are buying {cart.length} item(s) for <span>&#8358;</span>{tot} <br/>
            <Button className> Contiinue Shopping</Button> 
            </Jumbotron>
            <Row className="m-2">
                <Col lg="6" xs="12">    
                    <Form className="d-block m-auto">
                        <Label className="col-2 mt-1 pt-1"> Name </Label>
                        <input type="text" className="col-10 rounded p-1 mt-1 border" placeholder="Name"/><br/>

                        <Label className="col-2 mt-1 pt-1">Email</Label>
                        <input type="email" className="col-10 rounded p-1 mt-1 border" placeholder="Name"/><br/>
                        
                        <Label className="col-2 mt-1 pt-1"> Phone</Label>
                        <input type="tel" className="col-10 rounded p-1 mt-1 border" placeholder="Name"/><br/>
                        
                        <Label className="col-2 mt-1 pt-1"> Delivery <br/> address</Label>
                       <textarea className="col-10 rounded p-1 mt-1 border" placeholder="enter where you want us to delivery ">

                       </textarea>
                       <Label className="col-2 mt-1 pt-1"> Additional <br/> info</Label>
                       <textarea className="col-10 rounded p-1 mt-1 border" placeholder="is there any extra info you'd like to add ? e.g birthday plans">

                       </textarea>
                    </Form>          
                </Col>
                <Col lg="6" xs="12">
                   <Row className="m-2">
                   {check.map(({id,name, price, content, image, quantity, newPrice})=>{
                        return (
                            <Col xs="6" lg="4" className="">
                                <div className="bd-custom m-1">
                                    <img src={image} alt={name + " image"} width="100%" height="100px" className="" />
                                    <div className="text-center p-1">{quantity} {name}</div>
                                </div>
                            </Col >
                        )
                    })}
                   </Row>
                </Col>
            </Row>
            <Jumbotron >
              <Row>
                <Col lg="6" xs="12">
                    Total : <span>&#8358;</span>{tot} <br />
                    Delivery : <span>&#8358;</span>2000
                    {parseInt(tot)+ 2000}
                </Col>
                <Col lg="6" xs="12">
                    <Button>Make Payment</Button>
                </Col>
              </Row>
            </Jumbotron>
        </Container>

    </div>
    );
}

export default Checkout;