import React from 'react';
import { Link } from 'react-router-dom';

function Sidebar(props) {
    window.addEventListener('resize',(event)=>{
        document.getElementById("side").style.display = "none"
    })
    const style = {
        width:"45%", height:"100vh", top:"0", zIndex:"100", display:`${props.name}`,
        transtion:"opacity 2s, width 2s"
    }
    return (
        <div id="side" className="position-fixed bg-dark d-sm" style={style}>
            <div className="w-100 d-block">
                <Link className="text-custom nav-link p-2 pl-4 border w-100" to="/" >Home </Link>
                <Link className="text-custom nav-link p-2 pl-4 border w-100" to="/order" >Order Now </Link>
                <Link className="text-custom nav-link p-2 pl-4 border w-100" to="/Galleries" >Galleries </Link>
                <Link className="text-custom nav-link p-2 pl-4 border w-100" to="/contact" >Contact Us </Link>
                <Link className="text-custom nav-link p-2 pl-4 border w-100" to="/about" >About Us </Link>
            </div>
        </div>
    );
};

export default Sidebar;