import React from 'react';
import { Button } from 'reactstrap';
import FooterPage from './components/footer';
import Navbar from './components/Navbar';

function NotFound(props) {
    return (
        <div className="w-100">
            <Navbar />
            <div className="container col-lg-9 text-center p-lg-4 mt-2">
                {/* NotFound */}
                <Button onClick={()=>{window.history.back()}} className="m-3 bg-custom">Go back</Button>
                <img src="images/404-error-page-not-found.jpg" className="shadow" alt="Error page not found" width="100%"/> 
            </div>
            <FooterPage />
        </div>
    );
}

export default NotFound;