import React, { useEffect, useState } from 'react';
import Navbar from './Navbar';
import { Container, Jumbotron, Button, Row, Col } from 'reactstrap';
import Axios from 'axios';
import FooterPage from './footer';
import { useDispatch } from 'react-redux';
import { addItem } from './actions/actions';
function Order(props) {
    const [food, setFood] = useState([]);
    const [btn, setBtn ] = useState(false);
    const run = useDispatch()
    useEffect(() => {
        // Axios.get('http://localhost:5000/users')
        // .then(res => { setFood(res.data.items) } )
        Axios.get('https://relishcourt.com/users')
        .then(res => { setFood(res.data.items) } )
      },[]);
    const buy=(id, name, price, content, image, e)=>{
        setBtn(true)
        var data = {
            id : id,
            name : name,
            price : price,
            content : content, 
            image : image
        }
        run(addItem(data))
        // window.location.assign("/cart")
    }
    return (
        <div className="w-auto">
            <Navbar />
            <Container className=""> 
               <Jumbotron className='h2 text-custom mb-0'>Welcome to Relish Kitchen <br/> What would you like to Order?</Jumbotron>
               <Container className="m-auto">
                   <Row className="justify-content-around">
                    {food.map(({id, name, price, content, image})=>{
                        return(
                        <Col lg="4" md="4" xs="12" className="w-100" key={id}>
                            <div className="card m-1">
                                <img className="card-img-top" src={"./photos/"+image} alt={name + " image"} />
                                <div className="p-1">
                                    <h5 className="card-title">{name}</h5>
                                    <h6 className="card-title"> <span>&#8358;</span> {price}</h6>
                                    <p className="card-text">{content}</p>
                                    <Button className="btn btn-primary" onClick={(event)=>{buy(id, name, price, content, image, event)}}>{btn ? "Added to cart" : "Add to cart"}</Button>
                                </div>
                             </div>
                        </Col>
                        )
                    })}</Row>
               </Container>
           </Container> 
           <FooterPage bottom="auto"/>
        </div>
    );
}

export default Order;