export const increment =(x)=>{
    return {
        type : 'incre',
        hey : x
    }
}
export const decrement = () => {
    return {
        type : 'decre'
    }
}
export const addItem =(data)=>{
    let id = 0;
    return{
        type : 'add',
        id : id++,
        payload : data
    }
}
export const Remove =(id)=>{
    return{
        type : 'remove',
        id : id
    }
}
export const user_Def = (by,id,price,newPrice) =>{
    return{
        type : 'user_Def',
        id : id,
        by : by,
        price : price,
        newP : newPrice
    }
}